import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router';
import { Link as RouterLink } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  Checkbox,
  Divider,
  FormControlLabel,
  FormHelperText,
  Grid,
  Link,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableRow,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import { getCustomers } from 'src/actions/customerActions';
import { SET_CUSTOMER } from 'src/actions/jobActions';

const useStyles = makeStyles(() => ({
  root: {},
  editor: {
    '& .ql-editor': {
      height: 250
    }
  }
}));

function CustomerDetails({
  className,
  onBack,
  onNext,
  ...rest
}) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { customers } = useSelector((state) => state.customer);
  const [selCustomer, setSelCustomer] = useState(null);

  useEffect(() => {
    dispatch(getCustomers());
  }, []);

  const handleSubmit = (event) => {
    event.preventDefault();
    dispatch({
      type: SET_CUSTOMER,
      payload: selCustomer._id,
    });
    onNext();
  };

  const handleChangeCustomer = (event) => {
    const index = event.target.value;
    setSelCustomer(customers[index]);
  };

  if (!selCustomer && customers.length > 0) {
    setSelCustomer(customers[0]);
  }

  return (
    <form
      onSubmit={handleSubmit}
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Typography
        variant="h3"
        color="textPrimary"
      >
        Please select one customer
      </Typography>
      <Box mt={2}>
        <Card>
          <CardHeader title="Customer" />
          <Divider />
          <CardContent>
            <Box
              display="flex"
              minHeight={250}
            >
              <Grid
                item
              >
                <TextField
                  name="customer"
                  onChange={handleChangeCustomer}
                  select
                  SelectProps={{ native: true }}
                  variant="outlined"
                >
                  {customers.map((customer, index) => (
                    <option
                      key={customer._id}
                      value={index}
                    >
                      {`${customer.first_name} ${customer.last_name}`}
                    </option>
                  ))}
                </TextField>
              </Grid>
              { selCustomer && (
                <Grid>
                  <Box
                    ml={4}
                    md={4}
                    xl={3}
                    xs={12}
                  >

                    <Table>
                      <TableBody>
                        <TableRow>
                          <TableCell>Customer</TableCell>
                          <TableCell>
                            <Link
                              component={RouterLink}
                              to={`/app/customers/${selCustomer._id}/edit`}
                              target="_blank"
                            >
                              {`${selCustomer.first_name} ${selCustomer.last_name}`}
                            </Link>
                          </TableCell>
                        </TableRow>
                        <TableRow>
                          <TableCell>State</TableCell>
                          <TableCell>
                            <div>{selCustomer.state}</div>
                          </TableCell>
                        </TableRow>
                        <TableRow>
                          <TableCell>City</TableCell>
                          <TableCell>{selCustomer.city}</TableCell>
                        </TableRow>
                        <TableRow>
                          <TableCell>Street</TableCell>
                          <TableCell>
                            {selCustomer.street}
                          </TableCell>
                        </TableRow>
                      </TableBody>
                    </Table>
                  </Box>
                </Grid>
              )}
            </Box>
            <Box
              mt={6}
              display="flex"
            >
              {onBack && (
                <Button
                  onClick={onBack}
                  size="large"
                >
                  Previous
                </Button>
              )}
              <Box flexGrow={1} />
              {onNext && (
                <Button
                  color="secondary"
                  type="submit"
                  variant="contained"
                  size="large"
                >
                  Next
                </Button>
              )}
            </Box>

          </CardContent>
        </Card>
      </Box>
    </form>
  );
}

CustomerDetails.propTypes = {
  className: PropTypes.string,
  onNext: PropTypes.func,
  onBack: PropTypes.func
};

export default CustomerDetails;
