import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import axios from 'src/utils/axios';

import {
  ReactSortable, Sortable, MultiDrag, Swap
} from 'react-sortablejs';
import {
  Button,
  Container,
  Box,
  makeStyles
} from '@material-ui/core';
import { v4 as uuidv4 } from 'uuid';
import { createJob } from 'src/actions/jobActions';

// eslint-disable-next-line import/extensions
import { API_BASE_URL } from 'src/config';
import { SERVICE } from 'src/config/endpoint';
import CheckItem from 'src/components/CheckItem';

Sortable.mount(new MultiDrag(), new Swap());

const useStyles = makeStyles(() => ({
  root: {
    maxWidth: 1024,
  },
  checkList: {
    minHeight: 500,
    position: 'relative',
  },
  // addButton: {
  //   position: 'absolute',
  //   bottom: 20,
  //   right: 30,
  // },
  todo: {
    backgroundColor: 'white'
  },
  description: {
    maxWidth: '400px',
    marginLeft: '5px',
    width: '100%',
  }
}));

function Todos({
  className,
  onBack,
  onComplete,
}) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [service, setService] = useState(null);
  const [checkList, setCheckList] = useState(null);
  const { job } = useSelector((state) => (state.job));

  const getService = (serviceId) => {
    axios
      .get(`${API_BASE_URL}/${SERVICE.GET}/${serviceId}`)
      .then((response) => {
        if (response.data) {
          setService(response.data.service);
          if (response.data.service) {
            const { check_list } = response.data.service;
            setCheckList(check_list);
          }
        }
      });
  };

  useEffect(() => {
    if (job.service) {
      getService(job.service);
    }
  }, []);


  const handleComplete = () => {
    const data = { ...job, check_list: checkList };
    dispatch(createJob(data));
    onComplete();
  };

  const removeCheckItem = (id) => {
    const newCheckList = [...checkList];
    newCheckList.some((check_item, index) => {
      if (check_item._id === id) {
        newCheckList.splice(index, 1);
        setCheckList(newCheckList);
      }
    });
  };

  return (
    <Box
      className={classes.root}
    >
      <Box
        className={classes.checkList}
      >
        {checkList && (
        <ReactSortable
          multiDrag
          swap
          list={checkList}
          setList={setCheckList}
          className={clsx(classes.root, className)}
        >
          {checkList.map((todo, index) => (
            <CheckItem
              key={todo._id}
              checkItem={todo}
              updateItem={(newTodo) => {
                checkList[index] = newTodo;
                setCheckList(checkList);
              }}
              removeItem={(id) => { removeCheckItem(id); }}
              headings={service.headings}
            />
          ))}
        </ReactSortable>
        )}
        <Box
          display="flex"
          flexDirection="row-reverse"
          mt={2}
        >
          <Button
            color="secondary"
            variant="contained"
            onClick={() => {
              if (!checkList) {
                setCheckList([{
                  _id: uuidv4(),
                  form: 'input',
                  description: '',
                  expanded: true
                }]);
              } else {
                setCheckList([...checkList, {
                  _id: uuidv4(),
                  form: 'input',
                  description: '',
                  expanded: true
                }]);
              }
            }}
          >
            Add To Do
          </Button>
        </Box>
      </Box>
      <Box
        mt={6}
        display="flex"
      >
        {onBack && (
        <Button
          onClick={onBack}
          size="large"
        >
          Previous
        </Button>
        )}
        <Box flexGrow={1} />
        {onComplete && (
          <Button
            color="secondary"
            onClick={() => handleComplete()}
            size="large"
            variant="contained"
          >
            Complete
          </Button>
        )}
      </Box>
    </Box>
  );
}

Todos.propTypes = {
  className: PropTypes.string,
  onBack: PropTypes.func,
  onComplete: PropTypes.func,
};

export default Todos;
